/**
 * WordPress dependencies
 */
import { useEffect } from '@safe-wordpress/element';
import { useDispatch, useSelect } from '@safe-wordpress/data';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA, usePageAttribute } from '@nab/data';

export const StatusManager = (): null => {
	const [ isProcessingData ] = usePageAttribute(
		'heatmap/isProcessingData',
		false
	);
	const status = useStatus();
	const lock = useLock();

	useEffect( () => {
		lock( 'ready' !== status || isProcessingData );
	}, [ status ] );

	return null;
};

// =====
// HOOKS
// =====

const useStatus = () =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getPageAttribute( 'heatmap/dataStatus' )?.mode ??
			''
	);

const useLock = () => {
	const { lock, unlock } = useDispatch( NAB_DATA );
	return ( v: boolean ) => ( v ? lock() : unlock() );
};
