/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { Button, Dashicon, Spinner } from '@safe-wordpress/components';
import { useDispatch } from '@safe-wordpress/data';
import { _x, sprintf } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import {
	STORE_NAME as NAB_DATA,
	useAdminUrl,
	usePageAttribute,
} from '@nab/data';

/**
 * Internal dependencies
 */
import { useActiveExperiment } from '../hooks';
import { VariantSwitcher } from '../variant-switcher';

export const Header = (): JSX.Element => {
	const isHeatmapExperiment = useIsHeatmapExperiment();
	const isRunning = useIsRunning();
	const loadingDataMode = useLoadingDataMode();

	const [ _, triggerAction ] = usePageAttribute(
		'heatmap/postMessageProxy',
		false
	);
	const [ progress ] = usePageAttribute( 'heatmap/progress', 0 );
	const [ isStopping ] = usePageAttribute(
		'editor/isExperimentBeingStopped',
		false
	);
	const { stopExperiment } = useDispatch( NAB_DATA );

	const cancelDataLoading = () =>
		triggerAction( {
			plugin: 'nelio-ab-testing',
			type: 'stop-result-loading',
			mode: 'heatmap',
		} );

	if ( 'loading' === loadingDataMode ) {
		return (
			<div className="nab-heatmap-results-sidebar__header-actions">
				<BackButton />
			</div>
		);
	} //end if

	if ( 'still-loading' === loadingDataMode ) {
		return (
			<div className="nab-heatmap-results-sidebar__header-actions">
				<BackButton />
				<div className="nab-heatmap-results-sidebar__running-actions">
					{ !! progress ? (
						<span className="nab-heatmap-results-sidebar__progress">
							{ sprintf(
								/* translators: percentage */
								_x(
									'Fetching more data… (%1$d%%)',
									'text',
									'nelio-ab-testing'
								),
								Math.min( progress, 99 )
							) }
						</span>
					) : (
						<Spinner />
					) }
					<Button variant="link" onClick={ cancelDataLoading }>
						{ _x( 'Cancel', 'command', 'nelio-ab-testing' ) }
					</Button>
				</div>
			</div>
		);
	} //end if

	return (
		<div className="nab-heatmap-results-sidebar__header-actions">
			<BackButton />

			{ isHeatmapExperiment && isRunning && (
				<div className="nab-heatmap-results-sidebar__running-actions">
					<Button
						variant="primary"
						onClick={ stopExperiment }
						disabled={ isStopping }
						isBusy={ isStopping }
					>
						{ isStopping
							? _x( 'Stopping…', 'text', 'nelio-ab-testing' )
							: _x( 'Stop', 'command', 'nelio-ab-testing' ) }
					</Button>
				</div>
			) }

			{ ! isHeatmapExperiment && (
				<div className="nab-heatmap-results-sidebar__alt-switcher">
					<VariantSwitcher />
				</div>
			) }
		</div>
	);
};

const BackButton = () => {
	const backUrl = useBackUrl();
	return (
		<div className="nab-heatmap-results-sidebar__back">
			<a
				className="nab-heatmap-results-sidebar__back-link"
				href={ backUrl }
				title={ _x( 'Back…', 'command', 'nelio-ab-testing' ) }
			>
				<Dashicon icon="arrow-left-alt2" />
			</a>
		</div>
	);
};

// =====
// HOOKS
// =====

const useBackUrl = () => {
	const experimentsUrl = useAdminUrl( 'edit.php', {
		post_type: 'nab_experiment',
	} );
	const experiment = useActiveExperiment();
	if ( ! experiment ) {
		return;
	} //end if

	return experiment.type === 'nab/heatmap'
		? experimentsUrl
		: experiment.links.edit;
};

const useIsHeatmapExperiment = () =>
	useActiveExperiment()?.type === 'nab/heatmap';

const useIsRunning = () => useActiveExperiment()?.status === 'running';

const useLoadingDataMode = (): 'still-loading' | 'loading' | 'none' => {
	const value = usePageAttribute( 'heatmap/dataStatus', {
		mode: 'loading',
	} )[ 0 ].mode;
	switch ( value ) {
		case 'ready':
			return 'none';

		case 'still-loading':
			return value;

		default:
			return 'loading';
	} //end switch
};
