/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { useSelect } from '@safe-wordpress/data';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { AlternativeId, Dict, Heatmap, Maybe } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import { useSidebarVisibility } from '../hooks';
import { Sidebar } from '../sidebar';
import { View } from '../view';

export type LayoutProps = {
	readonly alternativeId: AlternativeId;
};

export const Layout = (): JSX.Element => {
	const [ isVisible ] = useSidebarVisibility();
	const contentUrl = useContentUrl();
	return (
		<div className="nab-heatmap-results">
			<Sidebar
				className={ classnames( {
					'nab-heatmap-results__sidebar': true,
					'nab-heatmap-results__sidebar--is-collapsed': ! isVisible,
				} ) }
				/* alternativeId={ alternativeId } */
			/>

			<View
				key="nab-heatmap-results__content"
				className={ classnames( {
					'nab-heatmap-results__content': true,
					'nab-heatmap-results__content--is-fullscreen': ! isVisible,
				} ) }
				contentUrl={ contentUrl }
			/>
		</div>
	);
};

// =====
// HOOKS
// =====

const useContentUrl = () =>
	useSelect( ( select ): Maybe< string > => {
		const { getPageAttribute, getExperiment } = select( NAB_DATA );

		const activeExperimentId = getPageAttribute(
			'editor/activeExperiment'
		);
		const experiment = getExperiment( activeExperimentId ?? 0 );
		if ( ! experiment ) {
			return undefined;
		} //end if

		if ( isHeatmap( experiment ) ) {
			return experiment.links.heatmap;
		} //end if

		const alternativeIndex =
			getPageAttribute( 'heatmap/activeAlternative' ) || 0;
		const alternative = experiment.alternatives[ alternativeIndex ];
		return alternative?.links.heatmap;
	} );

const isHeatmap = ( e: Dict ): e is Heatmap => 'nab/heatmap' === e.type;
