/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { useEffect } from '@safe-wordpress/element';
import {
	useDispatch,
	useSelect,
	select as doSelect,
} from '@safe-wordpress/data';
import { useInstanceId } from '@safe-wordpress/compose';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { BigError, LoadingAnimation } from '@nab/components';
import { STORE_NAME as NAB_DATA, usePageAttribute } from '@nab/data';
import type { HeatmapErrorRationale, HeatmapFrameAction } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import {
	useActiveFilter,
	useActiveMode,
	useActiveResolution,
	useDisabledFilterOptions,
	useHeatmapIntensity,
	useHeatmapOpacity,
} from '../hooks';

export type ViewProps = {
	readonly className: string;
	readonly contentUrl?: string;
};

export const View = ( { className, contentUrl }: ViewProps ): JSX.Element => {
	const instanceId = useInstanceId( View );
	usePostMessageProxyEffect( instanceId );
	useOpacityChangeEffect( instanceId );
	useIntensityChangeEffect( instanceId );
	useRenderIFrameEffect( instanceId );

	const [ resolution ] = useActiveResolution();
	const awaitRendererScript = useRendererScriptWaiter();

	return (
		<div className={ className }>
			<FeedbackMessage />
			<iframe
				id={ `nab-heatmap-results__iframe-${ instanceId }` }
				className={ `nab-heatmap-results__iframe nab-heatmap-results__iframe--${ resolution }` }
				title={ _x(
					'Content of the heatmap',
					'text',
					'nelio-ab-testing'
				) }
				src={ contentUrl }
				onLoad={ () => awaitRendererScript() }
			/>
		</div>
	);
};

// ============
// HELPER VIEWS
// ============

const FeedbackMessage = () => {
	const [ isProcessingData ] = usePageAttribute(
		'heatmap/isProcessingData',
		false
	);
	const status = useDataStatus();

	if ( ! status ) {
		return null;
	} //end if

	if ( isProcessingData ) {
		return (
			<div className="nab-heatmap-results__loader">
				<LoadingAnimation
					text={ _x(
						'Processing results…',
						'text',
						'nelio-ab-testing'
					) }
				/>
			</div>
		);
	} //end if

	if ( status.mode === 'still-loading' || status.mode === 'ready' ) {
		return null;
	} //end if

	if ( 'error' === status.mode ) {
		return (
			<div className="nab-heatmap-results__loader">
				<BigError text={ getErrorMessage( status.rationale ) } />
				{ 'promise-failed' === status.rationale && (
					<div className="nab-heatmap-results__adblock-explanation">
						{ _x(
							'If you’re using an adblocker on your browser, please disable it as it’s probably blocking Nelio A/B Testing’s requests, thus preventing the plugin from pulling your heatmap data.',
							'user',
							'nelio-ab-testing'
						) }
					</div>
				) }
			</div>
		);
	} //end if

	return (
		<div className="nab-heatmap-results__loader">
			<LoadingAnimation
				text={
					status.mode === 'loading'
						? _x( 'Fetching results…', 'text', 'nelio-ab-testing' )
						: _x( 'Loading page…', 'text', 'nelio-ab-testing' )
				}
			/>
		</div>
	);
};

// =====
// HOOKS
// =====

const useDataStatus = () =>
	useSelect( ( select ) =>
		select( NAB_DATA ).getPageAttribute( 'heatmap/dataStatus' )
	);

const useRendererScriptWaiter = () => {
	const { setPageAttribute } = useDispatch( NAB_DATA );
	return () =>
		void setTimeout( () => {
			const { getPageAttribute } = doSelect( NAB_DATA );
			const status = getPageAttribute( 'heatmap/dataStatus' );
			const isWaitingScript = 'waiting-script' === status?.mode;
			if ( isWaitingScript ) {
				setPageAttribute( 'heatmap/dataStatus', {
					mode: 'error',
					rationale: 'no-script',
				} );
			} //end if
		}, 30000 );
};

const useOpacityChangeEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ activeMode ] = useActiveMode();
	const [ opacity ] = useHeatmapOpacity();
	useEffect( () => {
		postMessage( {
			plugin: 'nelio-ab-testing',
			type: 'update-opacity',
			mode: activeMode,
			value: opacity,
		} );
	}, [ opacity ] );
};

const useIntensityChangeEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ intensity ] = useHeatmapIntensity();
	useEffect( () => {
		postMessage( {
			plugin: 'nelio-ab-testing',
			type: 'update-intensity',
			mode: 'heatmap',
			value: intensity,
		} );
	}, [ intensity ] );
};

const usePostMessageProxyEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ action, setAction ] = usePageAttribute(
		'heatmap/postMessageProxy',
		false
	);
	useEffect( () => {
		if ( action ) {
			postMessage( action );
			setAction( false );
		} //end if
	}, [ action ] );
};

const useRenderIFrameEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ activeFilter ] = useActiveFilter();
	const [ activeMode ] = useActiveMode();
	const [ activeResolution ] = useActiveResolution();
	const [ disabledFilterOptions ] = useDisabledFilterOptions();

	useEffect( () => {
		postMessage( {
			plugin: 'nelio-ab-testing',
			type: 'render-results',
			mode: activeMode,
		} );
	}, [
		activeFilter,
		activeMode,
		activeResolution,
		JSON.stringify( disabledFilterOptions ),
	] );
};

const usePostMessage =
	( instanceId: number | string ) => ( action: HeatmapFrameAction ) => {
		const id = `nab-heatmap-results__iframe-${ instanceId }`;
		const iframe = document.getElementById( id ) as HTMLIFrameElement;
		if ( iframe?.contentWindow?.postMessage ) {
			iframe.contentWindow.postMessage( action );
		} //end if
	};

// =======
// HELPERS
// =======

function getErrorMessage( rationale: HeatmapErrorRationale ) {
	switch ( rationale ) {
		case 'no-script':
			return _x( 'Unable to render heatmap', 'text', 'nelio-ab-testing' );

		case 'experiment-not-found':
			return _x( 'Experiment not found', 'text', 'nelio-ab-testing' );

		case 'alternative-not-found':
			return _x( 'Page not found', 'text', 'nelio-ab-testing' );

		case 'promise-failed':
			return _x(
				'There was an error when retrieving data from the cloud',
				'text',
				'nelio-ab-testing'
			);

		case 'unknown':
			return _x( 'Something went wrong', 'text', 'nelio-ab-testing' );
	} //end switch
} //end getErrorMessage()
