/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { PanelBody, Button } from '@safe-wordpress/components';
import { useSelect, useDispatch } from '@safe-wordpress/data';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA, usePageAttribute } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import { useIsLocked } from '../../hooks';

export const Advanced = (): JSX.Element | null => {
	const isLocked = useIsLocked();
	const isVisible = useIsVisible();
	const repositionData = useRepositionData();

	if ( ! isVisible ) {
		return null;
	} //end if

	return (
		<PanelBody
			initialOpen={ false }
			className="nab-advanced"
			title={ _x( 'Advanced', 'text', 'nelio-ab-testing' ) }
		>
			<p>
				{ _x(
					'Under some circumstances, when your page loads content dynamically, the results may be shifted from their correct position. Reset the view to re-render the results properly:',
					'user',
					'nelio-ab-testing'
				) }
			</p>

			<Button
				className="nab-advanced__reposition-button"
				disabled={ isLocked }
				variant="secondary"
				onClick={ repositionData }
			>
				{ _x( 'Reset View', 'command', 'nelio-ab-testing' ) }
			</Button>
		</PanelBody>
	);
};

// =====
// HOOKS
// =====

const useIsVisible = () =>
	useSelect(
		( select ) =>
			'scrollmap' !==
			select( NAB_DATA ).getPageAttribute( 'heatmap/activeMode' )
	);

const useRepositionData = () => {
	const [ mode ] = usePageAttribute( 'heatmap/activeMode', 'heatmap' );
	const { setPageAttribute } = useDispatch( NAB_DATA );
	return () =>
		mode !== 'scrollmap' &&
		setPageAttribute( 'heatmap/postMessageProxy', {
			plugin: 'nelio-ab-testing',
			type: 'recompute-click-coordinates',
			mode,
		} );
};
